import React from 'react';
import Card from "./Card"
// import {robots} from './robots';


const CardList =({robots})=>{

  let roboArr= robots.map((user,i)=>{
    return (
       <Card 
           key={i} 
           name={robots[i].name} 
           id={robots[i].id} 
           email={robots[i].email}/>
        );
    })
    

    return (
        <div>
        {roboArr}
        </div>
     );
}

export default CardList;